import React, { Component } from 'react';
import { connect } from 'react-redux';
import { mapStateToProps } from './state';
import PropTypes from 'prop-types';
import './styles.scss';

export default class UserApp extends Component {
    static propTypes = {
        studiesList: PropTypes.array,
        selectedStudy: PropTypes.object,
    };

    renderHeader() {
        return (
            <div className="header">
                <div className="layout-start">
                    <div className="header-name">
                        <div className="name-icon">
                            <img src="https://cdn2.iconfinder.com/data/icons/portrait/145/portrait-16-512.png" alt=" " height="26px" width="26px"/>
                        </div>
                        <div className="title">Warm guys</div>
                    </div>
                </div>
            </div>
        );
    }

    renderStudy(study){
        return (
        <div className='study'>
            {study.name}
        </div>
        )
    }


    renderStudies(studies) {
        return (<div className='studies-list'>
                    <div className='header'>
                         
                    </div>
                    <div className='studies-container'>
                        {studies? studies.map(this.renderStudy): null}
                    </div>
            </div>
        )
    }

    renderStudyDescription(study){
        return (<div className='study-description'>

        </div>)
    }

    renderHelloPage(){
        return (
            <div className='hello-page'>
                qweqwewqewqeqwe
            </div>
        )
    }

    render() {
        return (
            <div className='app-container'>
                {this.renderHeader()}
                <div className='container-body'>
                    {this.renderStudies()}
                    {this.props.selectedStudy? 
                        this.renderStudyDescription(this.props.selectedStudy)
                        :this.renderHelloPage()
                    }
                </div>
            </div>
        );
    }
}



